// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import sign from './api/sign.js'
import moment from 'moment'
import fun from './api/fun'


Vue.config.productionTip = false

Vue.prototype.Sign = sign
Vue.prototype.$moment = moment;
moment.locale('zh-cn');//需要汉化

import { Tab, Tabs, Switch, Swipe, SwipeItem, ActionSheet, Toast, Popup, Stepper, Icon, NavBar, Uploader } from 'vant';
Vue.use(Tab).use(Tabs).use(Switch).use(Swipe).use(SwipeItem).use(ActionSheet).use(Toast).use(Popup).use(Stepper).use(Icon).use(NavBar).use(Uploader);
Vue.use(fun);

// 路由切换 取消一切ajax请求
router.beforeEach((to, from, next) => {
  // Vue.prototype.$stopAjax()
  next();
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



