<template>
	<div class="main">
		<van-nav-bar
			title="5G体育"
			left-text="返回"
			:border="false"
			left-arrow
			@click-left="onClickLeft" />
		<div class="news">
			<div class="news_title">{{info.title}}</div>
			<div class="author_time">
				<div class="author_time_left">
					<span class="news_author">{{info.TaiTai98}}</span>
					<span class="news_time">{{info.addtime}}</span>
				</div>
				<div class="author_time_right" v-show="info.sqs_status == 1">
					<img src="../../assets/home/renzheng@2x.png" class="renzheng_img">
					<p class="renzheng">官方认证</p>
				</div>
			</div>
			<div class="news_content" v-html="info.content"> </div>
		</div>
		<!-- 精彩评论 -->
		<hr />
		<div class="good_comment">
			<div class="good_comment_title">
				<p class="small_line"></p>
				<strong class="">精彩评论</strong>
			</div>
			<div class="expert_info">
				<div class="expert_info_one border-b" v-for="(item, index) in ping" :key="item.id">
					<div class="expert_info_one_top">
						<div class="head_title">
							<img :src="item.avatar" class="head">
							<div class="title_single">
								<div class="title_single_nickname">{{item.nickname ? item.nickname : '5G体育网友'}}</div>
								<div class="fans">{{item.time}}</div>
							</div>
						</div>
						<div class="concern" @click="zanPing(item.id, index)">
							<span class="zan_num" :class="{actice : item.is_zan}">{{item.zan}}</span>
							<img src="../../assets/home/no_zan@2x.png" class="no_zan">
						</div>
					</div>
					<div class="expert_intro">
						{{item.content}}
						<div class="media" v-if="item.data">
							<img :src="item.data" style="width: 33.33%;">
						</div>
					</div>
				</div>
			</div>
		</div>
		<!-- 发布评论 -->
		<div class="release_comment">
			<div class="box">
				<div class="ipt" @click="postPing()">发表你的评论</div>
				<van-icon name="good-job-o" :class="{active : info.is_zan }" :info="info.praise ? info.praise : 0" @click="zanInfo" />
				<van-icon name="star-o" :class="{active : info.is_favor }" @click="setFavor" />
				<van-icon name="share" />
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				info: {
					is_zan: false,
					is_favor: false,
				},
				ping: []
			}
		},
		mounted() {

			this.getInfo();
		},
		methods: {
			onClickLeft() {
				this.$closeWebview();
			},
			getInfo() {
				this.$toast.loading({
					forbidClick: true
				});
				this.$ajax({
					type: 'post',
					url: '/index.php?g=Cliapi&m=Zixun&a=zixunDetail',
					data: { show_id: this.$route.query.id },
					success: (ret) => {
						this.info = ret.data ? ret.data : {};
						this.getPing();
						this.$toast.clear();
					}
				})
			},
			getPing() {
				this.$ajax({
					type: 'post',
					url: '/index.php?g=Cliapi&m=Show&a=getCommlist',
					data: { show_id: this.info.id,  _uid: this.$user('uid'), _sign: this.$user('sign')},
					success: (ret) => {
						this.ping = ret.data ? ret.data : [];
					}, complete: () => {
						this.$toast.clear();
					}
				})
			},
			setFavor() {
				this.$toast.success(this.info.is_favor ? '取消收藏' : '已收藏');
				this.info.is_favor = this.info.is_favor ? false : true;
				this.$ajax({
					type: 'post',
					url: '/index.php?g=Cliapi&m=Show&a=setShow',
					data: { id: this.info.id, type: 'favor', _uid: this.$user('uid'), _sign: this.$user('sign') }
				})
			},
			zanInfo() {
				this.$toast.success(this.info.is_zan ? '已取消点赞' : '已点赞');
				this.info.praise = this.info.is_zan ? parseInt(this.info.praise) - 1 : parseInt(this.info.praise) + 1;
				this.info.is_zan = this.info.is_zan ? false : true;

				this.$ajax({
					type: 'post',
					url: '/index.php?g=Cliapi&m=Zixun&a=zan',
					data: { show_id: this.info.id }
				})
			},
			zanPing(id, index) {
				this.$toast.success(this.ping[index].is_zan ? '已取消点赞' : '已点赞');
				this.$set(this.ping[index], 'zan', this.ping[index].is_zan ? parseInt(this.ping[index].zan) - 1 : parseInt(this.ping[index].zan) + 1);
				this.$set(this.ping[index], 'is_zan', this.ping[index].is_zan ? false : true);
				this.$ajax({
					type: 'post',
					url: '/index.php?g=Cliapi&m=Show&a=zanCommlist',
					data: { ping_id: id,  _uid: this.$user('uid'), _sign: this.$user('sign') }
				})
			},
			postPing() {
				this.$router.push({
					path: '/Posting',
					query: {
						show_id: this.info.id
					}
				});
			}
		},
	}
</script>

<style scoped>
	.media{ margin-top: 1rem;}

	.goback {
		width: 0.5rem;
		height: 1rem;
		color: #D8F2FE;
		padding-top: 1.2rem;
	}

	.title {
		font-size: 1.1rem;
		letter-spacing: 0.2rem;
		color: #EDFAFE;
		font-family: '微软雅黑';
		/* font-weight: 550; */
	}

	.news {
		padding: 1.5rem 1rem;
	}

	.news_title {
		text-align: left;
		font-size: 1.55rem;
		line-height: 2rem;
		color: #000;
	}

	.author_time {
		display: flex;
		justify-content: space-between;
		margin-top: 0.7rem;
		margin-bottom: 0.7rem;
	}

	.author_time_left {
		display: flex;
		align-items: center;
	}

	.author_time_right {
		display: flex;
	}

	.news_author {
		color: #01C0FF;
		font-size: 0.8rem;
	}

	.news_time {
		color: #8F9497;
		font-size: 0.8rem;
		margin-left: 0.3rem;
	}

	.renzheng_img {
		width: 1rem;
		height: 1rem;
	}

	.renzheng {
		font-size: 0.7rem;
		color: #9B9B9B;
		margin-left: 0.3rem;
	}

	.news_content {
		text-align: left;
		line-height: 2rem;
		text-indent: 2.1rem;
		overflow: hidden;
		font-size: 1.1rem;
	}

	.news_content>>>img {
		max-width: 100%;
		margin-top: 1rem;
		margin-bottom: 1rem;
		text-indent: -2rem;
		display: block;
	}

	.good_comment {
		margin-bottom: 77px;
	}

	.good_comment_title {
		font-size: 0.95rem;
		display: flex;
		padding: 0.8rem 0.8rem 0.2rem;
	}

	.small_line {
		width: 0.26rem;
		height: 1.2rem;
		background: #01BEFB;
		margin-right: 0.3rem;
		margin-top: 0.1rem;
		border-top-left-radius: 0.5rem;
		border-top-right-radius: 0.5rem;
		border-bottom-left-radius: 0.5rem;
		border-bottom-right-radius: 0.5rem;
	}

	.expert_info_one {
		padding: 0.7rem 0.8rem 0.7rem;
	}

	.expert_info_one_top {
		display: flex;
		justify-content: space-between;
	}

	.head_title {
		display: flex;
		justify-content: flex-start;
	}

	.head {
		width: 2.1rem;
		height: 2.1rem;
		border-radius: 50%;
	}

	.title_single {
		line-height: 1.4;
		margin-left: 0.3rem;
	}

	.title_single_nickname {
		font-size: 0.9rem;
		color: #01BFFC;
		text-align: left;
	}

	.fans {
		color: #AAAAAA;
		font-size: 0.65rem;
	}

	.concern {
		display: flex;
		align-items: center;
		margin-bottom: 0.5rem;
	}

	.zan_num {
		font-size: 0.8rem;
		color: #A1A1A1;
		margin-right: 0.4rem;
		margin-top: 0.2rem;
	}

	.no_zan {
		width: 1rem;
		height: 1rem;
	}

	.expert_intro {
		font-size: 1rem;
		color: #444;
		text-align: left;
		margin-top: 0.6rem;
	}

	.release_comment {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		padding: 0.6rem 0;
		background: #f9f9f9;
		z-index: 999999999;

	}

	.release_comment .box {
		padding: 0 1rem 0 0;
		display: flex;
		align-items: center;
		justify-content: space-around;
	}

	.release_comment i {
		font-size: 1.6rem;
		color: #333;
	}

	.release_comment i.active {
		color: #00BFFE;
	}

	.ipt {
		background: #f1f1f1;
		width: 50%;
		height: 2rem;
		border-radius: 5px;
		padding-left: 0.7rem;
		display: flex;
		align-items: center;
		font-size: 0.8rem;
		color: #C1C1C1;
	}


	.release_comment img {
		width: 1.3rem;
		height: 1.3rem;
	}
</style>


