import axios from 'axios'
import Qs from 'qs'

export default {
	install: (Vue) => {

		let pending = [];
		let cancelToken = axios.CancelToken;
		axios.defaults.timeout = 6 * 1000;
		axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
		// axios请求拦截器
		axios.interceptors.request.use(function (config) {

			Vue.prototype.$ajaxStop(config);

			config.cancelToken = new cancelToken((cancel) => {
				pending.push({ url: config.url, cancel: cancel });
			})

			return config

		}, function (error, error2) {
			return Promise.reject(error)
		});

		// axios响应拦截器
		axios.interceptors.response.use(function (response) {

			if (response.data.status < -100) {
				switch (response.data.status) {
					case -400:
						if (response.data.url) location.href = response.data.url;
						break;
					case -444:
						// alert(response.data.msg);
						break;
					case -401:
						if (response.data.url) location.href = response.data.url;
						break;
					default:
						console.error('未知错误类型')
						break;
				}
			}
			return response;

		}, function (error, code) {
			return Promise.reject(error)
		});

		// 封装axios ，用于未来处理重试、登陆等机制

		Vue.prototype.$ajax = (params) => {
			

			if (params.type == 'post') {
				axios.post(params.url, Qs.stringify(params.data))
					.then(response => {

						if (typeof (params.complete) == "function") {
							params.complete();
						}
						if (typeof (params.success) == "function") {
							params.success(response.data);
						}
					}).catch(err => {
						if (typeof (params.complete) == "function") {
							params.complete();
						}
						if (typeof (params.error) == "function") {
							params.error(err);
						}
					});
			} else {
				axios.get(params.url, {
					params: params.data
				}).then(response => {
					if (typeof (params.complete) == "function") {
						params.complete();
					}
					if (typeof (params.success) == "function") {
						params.success(response.data);
					}
				}).catch(err => {
					if (typeof (params.complete) == "function") {
						params.complete();
					}
					if (typeof (params.error) == "function") {
						params.error(err);
					}
				})
			}
		}

		Vue.prototype.$closeWebview = () => {
			try {
				if(navigator.userAgent.indexOf('Android') > -1){
					jsToApp.finishPage()
				}else{
					alert('closePage') 
				}
			} catch (error) {
				console.log(error)
			}
		}


		Vue.prototype.$user = (field) => {
			
			var user = {
				uid:27553,
				sign:'abc'
			}

			return field ? user[field] : user;
		}


		/**
		 * 获取urlget参数
		 * @author 郑薏玮 <715713881@qq.com>
		 **/
		Vue.prototype.$get = (name) => {
			var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
			var r = window.location.search.substr(1).match(reg);
			if (r != null) return unescape(r[2]); return null;
		}

		/**
		 * 中断所有进行中的ajax请求，切断阻塞
		 * @author 郑薏玮 <715713881@qq.com>
		 **/
		Vue.prototype.$ajaxStop = (config) => {

			pending.forEach((item, index) => {
				if (!!config) {
					if (item.url == config.url) {
						item.cancel('stopRequest : ' + item.url); //取消请求
						pending.splice(index, 1); //移除当前请求记录
					}
				} else {
					item.cancel('stopAjax'); //取消请求
					pending.splice(index, 1); //移除当前请求记录
				}
			});

		}

		/**
		 * 提取二维json对象中的某个一个字段组装成一个新的一维对象
		 * @param jsonObject object 二维json对象
		 * @param field string 需要提取的字段名称
		 * @return json
		 * @author 郑薏玮 <715713881@qq.com>
		 **/

		Vue.prototype.$json_column = (jsonObject, field) => {
			if (!jsonObject) return [];
			if (!field) console.error('json_column方法缺少参数');
			var data = [];
			jsonObject.forEach((item, i) => {
				data[i] = item[field];
			});
			return data;

		}


		/**
		 * 将二维数组中的某个字段作为键名
		 * @param jsonObject object 二维json对象
		 * @param field string 需要提取的字段名称
		 * @return json
		 * @author 郑薏玮 <715713881@qq.com>
		 **/

		Vue.prototype.$json_arr2key = (jsonObject, field) => {
			if (!jsonObject) return [];
			if (!field) console.error('json_column方法缺少参数');
			var data = {};
			for (var i = 0; i < jsonObject.length; i++) {
				data[jsonObject[i][field]] = jsonObject[i];
			}
			return data;
		}

		/**
		 * json对象转为url的get参数
		 * @author 郑薏玮 <715713881@qq.com>
		 **/
		Vue.prototype.$json2url = (param, key, encode) => {

			if (param == null) return '';
			var paramStr = '';
			var t = typeof (param);
			if (t == 'string' || t == 'number' || t == 'boolean') {
				paramStr += '&' + key + '=' + ((encode == null || encode) ? encodeURIComponent(param) : param);
			} else {
				for (var i in param) {
					var k = key == null ? i : key + (param instanceof Array ? '[' + i + ']' : '.' + i);
					paramStr += Vue.prototype.$json2url(param[i], k, encode);
				}
			}
			return paramStr;
		}

		/**
		 * 提取url中的域名部分
		 * @author 郑薏玮 <715713881@qq.com>
		 **/
		Vue.prototype.$url2domain = (url) => {
			var domain = url.split('/');
			if (domain[2]) {
				return domain[2];
			} else {
				return url
			}
		}


		Vue.prototype.$getDateStr = (AddDayCount) => {
			var dd = new Date();
			dd.setDate(dd.getDate() + AddDayCount);   //获取AddDayCount天后的日期
			var year = dd.getFullYear();
			var mon = dd.getMonth() + 1;                             //获取当前月份的日期
			var day = dd.getDate();
			return year + "-" + mon + "-" + day;
		}

		Vue.prototype.$formatBytes = (size = 0) => {

			var units = ['B', 'KB', 'MB', 'GB', 'TB', 'PB'];
			var i = 0;
			for (i = 0; size >= 1024 && i < 5; ++i) {
				size /= 1024;
			}
			return size.toFixed(2) + units[i];
		}

		// 格式化GTM时间
		Vue.prototype.$Gtm2Date = (sDate, forDay) => {
			var date = new Date(sDate);
			var seperator1 = "-";
			var seperator2 = ":";
			var month = date.getMonth() + 1;
			var strDate = date.getDate();
			var hours = date.getHours();
			var minutes = date.getMinutes();
			//月
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			//日
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			//时
			if (hours >= 0 && hours <= 9) {
				hours = "0" + hours;
			}
			//分
			if (minutes >= 0 && minutes <= 9) {
				minutes = "0" + minutes;
			}

			if (isNaN(date.getFullYear()) || isNaN(month) || isNaN(hours) || isNaN(minutes)) {
				alert('日期有误，请重新输入');
				return '';
			}

			if (forDay) {
				//格式化后日期为：yyyy-MM-dd HH:mm:ss
				var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + hours + seperator2 + minutes;
			} else {
				//格式化后日期为：yyyy-MM-dd HH:mm:ss
				var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate;
			}

			return currentdate;
		}



		/**
		 * 类似于php或jQuery的in_array
		 * @author 郑薏玮 <715713881@qq.com>
		 **/
		Vue.prototype.$inArray = (search, array) => {
			for (var i in array) {
				if (array[i] == search) {
					return i;
				}
			}
			return -1;
		}


		Vue.prototype.$gtm2StrDate = (time) => {
			let date = new Date(time)
			let Str = date.getFullYear() + '-' +
				(date.getMonth() + 1) + '-' +
				date.getDate()
			return Str
		}


		/**
		 * 上个与第一天
		 * @author 郑薏玮 <715713881@qq.com>
		 **/
		Vue.prototype.$getDate = (type) => {
			var datetime;
			switch (type) {
				case 'prev-week':
					datetime = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate() - 7);
					break;
				case 'prev-month-firstdate':
					datetime = new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1);
					break;
				case 'prev-month-enddate':
					var date = new Date();
					var day = new Date(date.getFullYear(), date.getMonth(), 0).getDate();
					datetime = new Date(new Date().getFullYear(), new Date().getMonth() - 1, day);
					break;
				case 'prev-year-firstdate':
					datetime = new Date(new Date().getFullYear() - 1, new Date().getMonth(), 1);
					break;
				case 'prev-year-enddate':
					var date = new Date();
					var day = new Date(date.getFullYear() - 1, date.getMonth(), 0).getDate();
					datetime = new Date(new Date().getFullYear() - 1, new Date().getMonth(), new Date().getDate());
					break;
			}

			return datetime;
		}



	}
}


