<template>
  <div id="app"> 
    <transition :name="transitionName">
      <router-view v-if="isRouterAlive" />
		</transition>
  </div>
</template>

<script>
  export default {
    name: 'App',
    provide() {
      return {
        reload: this.reload
      }
    },
    data() {
      return {
        
        transitionName:'',
        style: '',
        isRouterAlive: true
      }
    },
    watch: {
			$route(to, from) {
				if (to.meta.index == from.meta.index || to.meta.index - from.meta.index === 0) {
					this.transitionName = ''
					return true;
				}
				if (to.meta.index > from.meta.index) {
					this.transitionName = 'slide-left';
				} else {
					this.transitionName = 'slide-right';
				}
			}
		},
    methods: {
      reload() {
        this.isRouterAlive = flase;
        this.$nextTick(function () {
          this.isRouterAlive = true
        })
      }
    },
  }
</script>

<style>
  html,
  body,
  header,
  section,
  footer,
  div,
  ul,
  ol,
  li,
  img,
  a,
  span,
  em,
  del,
  legend,
  center,
  strong,
  var,
  fieldset,
  form,
  label,
  dl,
  dt,
  dd,
  cite,
  input,
  hr,
  time,
  mark,
  code,
  figcaption,
  figure,
  textarea,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
    border: 0;
    padding: 0;
    font-style: normal;
  }

  html,
  body {
    -webkit-touch-callout: none;
    -webkit-text-size-adjust: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-user-select: none;
    background-color: #fff;
  }

  nav,
  article,
  aside,
  details,
  main,
  header,
  footer,
  section,
  fieldset,
  figcaption,
  figure {
    display: block;
  }

  img,
  a,
  button,
  em,
  del,
  strong,
  var,
  label,
  cite,
  small,
  time,
  mark,
  code,
  textarea {
    display: inline-block;
  }

  ol,
  ul {
    list-style: none;
  }

  input,
  button,
  textarea {
    border: 0;
    margin: 0;
    padding: 0;
    font-size: 1rem;
    line-height: 1rem;
    /*-webkit-appearance:none;*/
    background-color: rgba(0, 0, 0, 0);
  }

  span {
    display: inline-block;
  }

  a:active,
  a:hover {
    outline: 0;
  }

  a,
  a:visited {
    text-decoration: none;
  }

  label,
  .wordWrap {
    word-wrap: break-word;
    word-break: break-all;
  }

  table {
    border-collapse: collapse;
    border-spacing: 0;
  }

  td,
  th {
    padding: 0;
  }

  #app {
    width: 100%;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }

  hr {
    height: 8px;
    background: #f5f5f5;
  }

.slide-left-enter-active,.slide-left-leave-active,.slide-right-enter-active,.slide-right-leave-active{will-change:transform;transition:all .3s;position:absolute}
.slide-right-enter{opacity:0;transform:translate3d(-20%,0,0)}
.slide-right-leave-active{opacity:0;transform:translate3d(20%,0,0)}
.slide-left-enter{opacity:0;transform:translate3d(20%,0,0)}
.slide-left-leave-active{opacity:0;transform:translate3d(-20%,0,0)}
.main{ width: 100%;}
  /*边框样式*/

  .border-b,
  .border-l,
  .border-r,
  .border-t,
  .border {
    position: relative
  }

  .border-t::before {
    -webkit-transform: scaleY(.33);
    transform: scaleY(.33);
    -webkit-transform-origin: 0 1;
    transform-origin: 0 1;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, .1);
    display: block;
    z-index: 2;
  }

  .border-r::before {
    -webkit-transform: scaleX(.33);
    transform: scaleX(.33);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 1px;
    background-color: rgba(0, 0, 0, .1);
    display: block;
    z-index: 2;
    transform-origin: 50% 100%
  }

  .border-b::after {
    -webkit-transform: scaleY(.33);
    transform: scaleY(.33);
    -webkit-transform-origin: 0 1;
    transform-origin: 0 1;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0px;
    height: 1px;
    width: 100%;
    background-color: rgba(0, 0, 0, .2);
    display: block;
    z-index: 100;
  }

  .border-l::after {
    -webkit-transform: scaleX(.33);
    transform: scaleX(.33);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    pointer-events: none;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 1px;
    background-color: rgba(0, 0, 0, .1);
    display: block;
    z-index: 2;
    transform-origin: 50% 100%
  }

  .border:before {
    -webkit-transform: scale(0.33);
    transform: scale(0.33);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #ccc;
    padding: 1px;
    z-index: 2;
    width: 303%;
    height: 303%;
  }

  .van-nav-bar{ background: #00BFFE; color: #fff;}
  .van-nav-bar  .van-icon,
  .van-nav-bar .van-nav-bar__text,
  .van-nav-bar .van-nav-bar__title
  { color: #fff;}
  .van-nav-bar__text:active{ background: rgb(12, 160, 209);}
</style>


