<template>
	<div class="main">
		<van-nav-bar
			title="发布帖子"
			left-text="返回"
			right-text="发表"
			:border="false"
			left-arrow
			@click-left="onClickLeft" @click-right="onClickRight" />

		<section style="padding: 20px;">
			<div class="posting">
				<div class="posting_title border-b">
					<input type="text" v-model="form.title" placeholder="帖子标题（选填，最多36个字符）" class="ipt">
				</div>
				<div class="posting_content">
					<textarea v-model="form.content" class="txa" cols="30" rows="10" placeholder="发表帖子正文"></textarea>
				</div>
				<div style="padding: 0 0 10px 15px;"><van-uploader :after-read="afterRead" accept="image/*" :max-count="1" :preview-image="true" :preview-full-image="true" v-model="fileList" :before-delete="deleteImg" /></div>
			</div>
			
		</section>

	 
	</div>
</template>
<script>
	export default {
		data() {
			return {
				fileList: [],
				form: {
					show_id: this.$route.query.show_id,
					parent_id: this.$route.query.parent_id ? this.$route.query.parent_id : null,
					content: null,
					title: null,
					data: 0,
					_uid: 27553,
					_sign: 1,
				}
			}
		},
		mounted() {

		},
		methods: {
			onClickLeft() {
				this.$router.back();
			},
			deleteImg() {
				this.form.data = null;
				this.fileList = [];
			},
			afterRead(file) {
				this.$toast.loading({
					message: '上传中',
					forbidClick: true
				});
				file.status = 'uploading';
				file.message = '上传中...';
				this.$ajax({
					type: 'post',
					url: '/index.php?g=Cliapi&m=Member&a=uploadImg',
					data: { _uid: this.$user('uid'), _sign: this.$user('sign'), data: '["' + file.content + '"]' },
					success: (ret) => {
						this.form.data = ret.data[0];
						if (!ret.status) {
							file.status = 'done';
							return this.$toast.fail(ret.info);
						}
						this.$toast.success(ret.info);
					}, error: () => {
						file.status = 'failed';
						file.message = '上传失败';
						this.$toast.fail('上传失败');
					}
				})
			},
			onClickRight() {
				this.$toast.loading({
					forbidClick: true
				});
				this.$ajax({
					type: 'post',
					url: '/index.php?g=Cliapi&m=Show&a=addComment',
					data: this.form,
					success: (ret) => {
						if (!ret.status) {
							return this.$toast.fail(ret.info);
						}
						this.$toast.success(ret.info);
						setTimeout(() => {
							this.$router.back(-1)
						}, 800);

					}, error: () => {
						this.$toast.fail('发布失败');
					}
				})
			}
		},
	}
</script>
<style scoped>
	.update{ background: #fff; width: 100%; height: 100px;}
	
	.cancel {
		width: 2rem;
		height: 1rem;
		color: #D8F2FE;
	}

	.release {
		width: 2rem;
		height: 0.5rem;
		color: #D8F2FE;
	}

	.title {
		font-size: 1.1rem;
		letter-spacing: 0.2rem;
		color: #EDFAFE;
		font-weight: 100;
	}

	.posting {
		/* border: 1px solid red; */
		border-radius: 1rem;
		box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
	}

	.posting_title {
		height: 3.5rem;
		padding: 0 22px;
	}

	.ipt {
		width: 100%;
		height: 100%;
	}

	.posting_content {
		padding: 20px;
	}

	.txa {
		width: 100%;
		height: 100%;
		line-height: 1.6;
		/* border: 1px solid green; */
	}

	.more_function {
		padding-left: 20px;
		padding-bottom: 10px;
	}

	.more_function i {
		font-size: 30px;
	}
</style>


